* {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

body, html {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(/isitkeyword/static/media/bg.4e57ba98.svg) no-repeat fixed center;
    background-size: cover;
}
.in-center {
    position: absolute;
    top: 55%;
    left: 50%;
    -webkit-transform: translate(-50%, -65%);
            transform: translate(-50%, -65%);
}

.container {
    width: 40%;
}

h1 {
    text-align: center;
    font-size: 4em;
    margin: 0 0 64px 0;
    text-transform: uppercase;
    color: rgb(146, 146, 146);
}

.question-icon {
    color: #d7d7d7;
}

.question-icon:hover {
    color: rgb(73, 73, 73);
    cursor: pointer;
    visibility: visible;
}

@media only screen and (min-width: 978px){
    .tooltip {
        position: absolute;
        bottom: 0;
        color: #929292;
    }
 }

@media only screen and (max-width: 977px) {
    .tooltip {
        position: absolute;
        bottom: 0;
        color: #929292;
    }
}

.contact {
    color: #92453f;
}
.input-field {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

.form-field {
    margin: 16px;
    display: -webkit-inline-flex;
    display: inline-flex;
    border-radius: 10px;
    /*background: blue;*/
    width: 100%;
    height: 40px;
}

.input-in {
    padding: 6px 16px;
    /*padding: 6px;*/
    overflow: hidden;
    border-radius: 4px;
}

input[type=text] {
    width: 100%;
    /*padding: 12px 100%;*/
    /*margin: 8px 0;*/
    border: 1px solid #bbb;
    border-radius: 4px;
    box-sizing: border-box;
}

.button-fa {
    overflow: hidden;
}

.search-icon {
    font-size: 1em;
}

p {
    text-align: center;
}


.shown {
    /*visibility: visible;*/
    color: #0b9c06;
    border-bottom: solid #bbbbbb 2px;
    text-align: center;
    margin: 5px;
}

.hidden {
    color: black;
    border-bottom: solid #bbbbbb 2px;
    text-align: center;
    margin: 5px;
}

.checkBoxes {
    width: 30%;
    border-right: solid #bbbbbb 5px;
}

label {
    width: 100%;
}

.pickLang {
    visibility: hidden;
}
.keyword-container {
    position: absolute;
    /*top: 100%;*/
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.case-sensitive-error {
    color: #bb3b00;
    margin: 5px;
    border-bottom: solid 5px;
    width: 100%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    transition: .5s;
}

.case-sensitive-error:hover {
    color: #772700;
    width: 120%;
    transition: .5s;
    cursor: pointer;
}


.to-output {
    color: #0b9c06;
    margin: 5px;
    border-bottom: solid 5px;
    width: 100%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    transition: .5s;
}

.to-output:hover {
    color: #034002;
    width: 120%;
    transition: .5s;
    cursor: pointer;
}
