.in-center {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -65%);
}

.container {
    width: 40%;
}

h1 {
    text-align: center;
    font-size: 4em;
    margin: 0 0 64px 0;
    text-transform: uppercase;
    color: rgb(146, 146, 146);
}

.question-icon {
    color: #d7d7d7;
}

.question-icon:hover {
    color: rgb(73, 73, 73);
    cursor: pointer;
    visibility: visible;
}

@media only screen and (min-width: 978px){
    .tooltip {
        position: absolute;
        bottom: 0;
        color: #929292;
    }
 }

@media only screen and (max-width: 977px) {
    .tooltip {
        position: absolute;
        bottom: 0;
        color: #929292;
    }
}

.contact {
    color: #92453f;
}